<template>
  <div class="container">
    <div class="header">
      <div class="header">
      <h5><strong> Menús</strong></h5>
    </div>
    </div>
    <form @submit="postData" class="form" method="post">
      <div class="mb-3 input-col">
        <div style="width: 47%">
          <label class="form-label" for="nombreMenu">Tipos de menú</label>
          <div class="container-btn">
            <input
              v-model="menu.nombre"
              id="form-control"
              class="form-control mr-sm-2"
              type="text"
              name="nombreMenu"
              title="nombreMenu"
              placeholder="Vegetariano"
              size="sm"
            />

            <button
              @click="agregarMenu(menu.nombre)"
              style="margin-top: 3.5px"
              type="submit"
              size="sm"
              class="my-btn btn btn-dark my-2 my-sm-1"
            >
              Agregar
            </button>
          </div>
          <br>
          <ol>
            <li
              style="display: flex; position: relative; left: -3%"
              v-for="menu in menus"
              :key="menu.ID_Menu"
            >
              <b-icon
                class="del-icon"
                icon="x-circle"
                scale="1"
                v-on:click="borrarMenu(menu.ID_Menu)"
                variant="danger"
              />
              {{ menu.Tipo_Menu }}
            </li>
          </ol>
          <!--button
            type="button"
            style="margin-top: 3.5px; margin-left: 1%"
            class="btn btn-md btn-warning resize-del"
            v-on:click="borrarMenus"
          >
            Borrar
          </button>
          -->
        </div>
      </div>    
  </form>    
  </div>
</template>

<script>
// <ol v-if="menus.length"> </ol>
import axios from 'axios'
import Vue from 'vue'
import VueAxios from 'vue-axios'
import Config from '@/api/config';
Vue.use(VueAxios, axios)

export default {
    name: "Menus",
    data() {
        return {
        show: true,
      
        menu: {
        nombre: undefined
        },
      };
    },
    methods: {
        async postData(e){
          this.axios.post(`${Config.BASE_URL}/api/menu/`, {
          menu:{ ...this.menu}})
          /*{
          headers: {
          'Content-Type': 'application/json'
          }
          })*/
          .then((result) =>{
            //this.$router.push({name:'Menus'})
            console.warn(result)
            this.$router.push({name: 'EventosInicioA'})
          })
          e.preventDefault();
        },

        /*agregarMenu(e){
          axios.post(`${Config.BASE_URL}/api/menu/`, {
            menu: this.menu})
            .then((result) =>{
              console.warn(result)
            })
            e.preventDefault();
        },*/
        agregarMenu() {
        this.$store.commit("addMenu", {...this.menu})
        //this.$store.dispatch("SetMenus")
        //this.menu.Tipo_Menu = '';
        //this.clearMenu();
        },
        borrarMenu(id) {
          const response = confirm(`¿Estas seguro que quieres borrar este menú?`)
          if(response){
            this.axios.delete(`${Config.BASE_URL}/api/menu/`+ id)
              .then(() =>{
            //console.warn(result)
            //this.$store.state.users = result.data
            this.$store.dispatch("getMenus"); 
              })
          }    
        },
        },
        clearMenu() {
        //this.menus = this.menus.filter((i) => i !== menu);
        this.menu.Tipo_Menu = undefined;
        },
      mounted (){
      this.$store.dispatch("getMenus");     
    },
      computed: {
        menus() {
            return this.$store.state.menus;
          }
        },
    }


</script>

<style scoped>
.header {
  display: flex;
  justify-content: center;
  margin-top: 2%;
}

.del-icon {
  margin-right: 2%;
  cursor: pointer;
}

.container {
  display: grid;
  justify-content: center;
  grid-template-areas:
    "header"
    "form"
    "footer";
  grid-template-rows: 7vh auto 7vh;
}

.header {
  grid-area: header;
}

.form {
  grid-area: form;
  justify-self: center;

  width: 80vw;
  min-height: 60vh;
  padding: 4%;
}

#form-ctrl {
  width: 40% !important;
  margin: 3px;
}

.container-btn {
  display: flex;
  flex-wrap: wrap;
}

.my-btn {
  display: grid !important;
  block-size: 45px !important;
}

.input-col {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.footer {
  grid-area: footer;
}

ol {
  list-style: circle;
}

.button-with-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container-registro {
  display: flex;
  justify-content: space-around;
}

@media only screen and (max-width: 540px) {
  .resize {
    display: grid !important;
    grid-template-areas:
      "titulo" "titulo"
      "borrar" "agregar";
    grid-gap: 5%;
    padding-bottom: 10%;
    position: relative;
  }

  .resize-tit {
    grid-area: titulo;
    justify-self: center;
  }

  .resize-del {
    grid-area: borrar;
    width: 100%;
  }

  .rezie-add {
    grid-area: agregar;
    width: 100%;
  }
}

</style>